<template>
  <div class="popup-container popup-container-hongbao" effect="hongbao"
       :class="{'popup-showing active': state == 1, 'popup-showing popup-hidden': state == 2}">
    <div class="popup von-popup popup-open-hongbao">
      <div class="popup-body">
        <div class="text-1">
          <div class="letter-spacing">初次见面</div>
          <div>送您一个新人红包</div>
        </div>

        <div class="btn-open-hongbao-backface" :class="{'flip-reverse': this.doAnimate}"></div>
        <div class="btn-open-hongbao" :class="{'flip': this.doAnimate}" @click="openHongbao()"></div>
        <div class="old-entrance" @click="forward('login')">我是老用户<i class="arrow-r"></i></div>
      </div>
    </div>
  </div>
</template>
<script>
  const popup_leave_duration = 300

  function preventDefault (e) {
    e.preventDefault()
  }

  export default {
    props: {
      onOpenHongbao: Function
    },

    data () {
      return {
        state: 0, // 0: hidden, 1: showing, 2: active
        doAnimate: false
      }
    },

    methods: {
      forward () {
        this.hide()
        let route = {
          path: '/login',
          query: {backroute: '/'}
        }
        $router.forward(route)
      },

      show () {
        this.state = 1
        document.body.classList.add('popup-open')
        $backdrop.show()
        document.addEventListener('touchmove', preventDefault)

        return new Promise((resolve, reject) => {
          this.$on('PopupButtonClickEvent', (data) => {
            resolve(data.buttonIndex)
          })
        })
      },

      hide () {
        this.state = 2

        $backdrop.hide()
        setTimeout(() => {
          document.body.classList.remove('popup-open')
        }, 300)

        document.removeEventListener('touchmove', preventDefault)

        setTimeout(() => {
          this.state = 0
        }, popup_leave_duration)
      },

      openHongbao () {
        this.doAnimate = true
        if (this.onOpenHongbao) this.onOpenHongbao()
      }
    }
  }
</script>
<style lang="scss">
  @import "../../scss/variables";
  @import "../../scss/mixins";

  .popup-open-hongbao {
    .popup-body {
      width: 275px;
      height: 332px;

      @include background-image(url(//assets.qkcdn.com/images/52ccfce1416673aa1bea2ac6d16c818a.png));

      margin-top: -1px;
      padding: 59px 30px 30px 30px !important;
      color: #FFF;
      position: relative;
      overflow: hidden;

      .btn-open-hongbao, .btn-open-hongbao-backface {
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background: #fed7b0;
        position: absolute;
        left: 98px;
        top: 136px;
      }

      .btn-open-hongbao {
        z-index: 2;

        @include background-image(url(//assets.qkcdn.com/images/77eb3be9441625c4ce19e18d8c680a4e.png));
      }

      .btn-open-hongbao-backface {
        z-index: 1;

        @include background-image(url(//assets.qkcdn.com/images/475967188ede87f882df1cd7637ac633.png));
      }

      .text-1 {
        position: absolute;
        top: 42px;
        width: 210px;
        color: #fed7b0;
        text-align: center;

        div {
          @include font-regular();

          font-size: 22px;
          line-height: 30px;

          &.letter-spacing {
            margin-bottom: 5px;
          }
        }
      }

      .text-1 {
        z-index: 1;
      }
    }
  }

  /* 动画 */

  @-webkit-keyframes bounceInDown {
    from, 60%, 75%, 90%, to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }

    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, -3000px, 0);
      transform: translate3d(0, -3000px, 0);
    }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(0, 25px, 0);
      transform: translate3d(0, 25px, 0);
    }

    75% {
      -webkit-transform: translate3d(0, -10px, 0);
      transform: translate3d(0, -10px, 0);
    }

    90% {
      -webkit-transform: translate3d(0, 5px, 0);
      transform: translate3d(0, 5px, 0);
    }

    to {
      -webkit-transform: none;
      transform: none;
    }
  }

  @keyframes bounceInDown {
    from, 60%, 75%, 90%, to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }

    0% {
      opacity: 0;
      -webkit-transform: translate3d(0, -3000px, 0);
      transform: translate3d(0, -3000px, 0);
    }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(0, 25px, 0);
      transform: translate3d(0, 25px, 0);
    }

    75% {
      -webkit-transform: translate3d(0, -10px, 0);
      transform: translate3d(0, -10px, 0);
    }

    90% {
      -webkit-transform: translate3d(0, 5px, 0);
      transform: translate3d(0, 5px, 0);
    }

    to {
      -webkit-transform: none;
      transform: none;
    }
  }

  .bounceInDown {
    -webkit-animation-name: bounceInDown;
    animation-name: bounceInDown;
  }

  .popup-container[effect="hongbao"] {
    &.popup-hidden .popup {
      .popup-body > * {
        display: none;
      }

      -webkit-animation-name: slideUpOut;
      animation-name: slideUpOut;
      -webkit-animation-duration: 0.25s;
      animation-duration: 0.25s;
      -webkit-animation-timing-function: ease;
      animation-timing-function: ease;
    }

    &.active .popup {
      background: none;

      .popup-body > * {
        visibility: visible;
      }

      -webkit-animation-name: bounceInDown;
      animation-name: bounceInDown;
      -webkit-animation-duration: 0.75s;
      animation-duration: 0.75s;
    }
  }

  @keyframes flip {
    0% {
      transform: perspective(320px) rotateY(0deg);
    }

    33% {
      transform: perspective(320px) rotateY(180deg);
    }

    66% {
      transform: perspective(320px) rotateY(359deg);
    }

    100% {
      transform: perspective(320px) rotateY(540deg);
    }
  }

  @keyframes flip-reverse {
    0% {
      transform: perspective(320px) rotateY(540deg);
    }

    33% {
      transform: perspective(320px) rotateY(359deg);
    }

    66% {
      transform: perspective(320px) rotateY(180deg);
    }

    100% {
      transform: perspective(320px) rotateY(0deg);
    }
  }

  .flip, .flip-reverse {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-animation-duration: 0.5s;
    animation-duration: 0.5s;
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
    animation-fill-mode: forwards;
  }

  .flip {
    animation-name: flip;
  }

  .flip-reverse {
    animation-name: flip-reverse;
  }

  .old-entrance {
    position: absolute;
    height: 18px;
    font-size: 13px;
    letter-spacing: 0.2px;
    color: #fed7b0;
    left: 101px;
    bottom: 18px;

    @include font-regular();

    .arrow-r {
      display: inline-block;
      width: 5px;
      height: 8px;
      background: url('//assets.qkcdn.com/images/5e07500a6553e4024af255d43398ca1e.png') no-repeat;
      background-size: 100% 100%;
      margin-left: 4px;
    }
  }
</style>



